export default "<template>\n  <Sandbox\n    id=\"example\"\n    title=\"Example\"\n  >\n    <Preview>\n      <VCheckbox\n        id=\"checkbox-demo\"\n        v-model=\"isChecked\"\n        :bordered=\"bordered\"\n        :rounded=\"rounded\"\n        :dark=\"dark\"\n        :light=\"light\"\n        :disabled=\"disabled\"\n        :error=\"error\"\n        :label=\"label\"\n      />\n    </Preview>\n    <template #controls>\n      <Knobs>\n        <KnobBoolean\n          v-model=\"dark\"\n          label=\"Dark\"\n        />\n        <KnobBoolean\n          v-model=\"light\"\n          label=\"Light\"\n        />\n        <KnobBoolean\n          v-model=\"bordered\"\n          label=\"Bordered\"\n        />\n        <KnobBoolean\n          v-model=\"rounded\"\n          label=\"Rounded\"\n        />\n        <KnobBoolean\n          v-model=\"disabled\"\n          label=\"Disabled\"\n        />\n        <KnobString\n          v-model=\"label\"\n          label=\"Label\"\n        />\n        <KnobString\n          v-model=\"error\"\n          label=\"Error\"\n        />\n      </Knobs>\n    </template>\n  </Sandbox>\n</template>\n\n<script>\nimport {\n  Preview,\n  Sandbox,\n  Knobs,\n  KnobBoolean,\n  KnobString\n} from '@/docs/components';\nimport {\n  VCheckbox\n} from '@/components';\n\nexport default {\n  name: 'CheckboxExample',\n  components: {\n    Preview,\n    Sandbox,\n    Knobs,\n    KnobBoolean,\n    KnobString,\n    VCheckbox\n  },\n  data () {\n    return {\n      isChecked: true,\n      bordered: false,\n      round: false,\n      rounded: true,\n      roundedLg: false,\n      roundedSmall: false,\n      dark: false,\n      light: true,\n      disabled: false,\n      error: '',\n      hint: '',\n      label: 'some label...'\n    };\n  }\n};\n</script>\n";